const currencyTop = document.querySelectorAll(".currency__item_top");
const currencyBottom = document.querySelectorAll(".currency__item_bottom");
const currencyListTop = document.querySelector("#currency-list-top");
const currencyListTBottom = document.querySelector("#currency-list-bottom");

currencyTop.forEach((n) => {
  n.addEventListener("click", () => {
    currencyListTop.classList.toggle("currency__items_active");
  });
});

currencyBottom.forEach((n) => {
  n.addEventListener("click", () => {
    currencyListTBottom.classList.toggle("currency__items_active");
  });
});
